<template>
  <div id="app">
    <router-view/>
    <Header/>
    <Emotion/>
    <Footer/>
  </div>
</template>

<script>
import Header from './components/Header';
import Smile from './components/Smile';
import Statistics from './components/Statistics';
import Footer from './components/Footer';
import Emotion from './components/Emotion';
import axios from 'axios';

export default {
  name: 'App',
  components: {
    Header,
    Emotion,
    Statistics,
    Footer
  },
  data() {
    return {
      allowPosition : false,
    }
  },
  
  mounted() {
    this.initAllowLoc();
  },

  methods : {
    initAllowLoc : function() {
      window.navigator.geolocation.getCurrentPosition(this.current_position);
    },

    current_position : function(position) {
      var msg;
      msg = "Latitude: " + position.coords.latitude + ", " + "Longitude: " + position.coords.longitude;
      //alert(msg);
      this.fetchRerverseGeocoding();
    },

    fetchRerverseGeocoding : function() {
      axios({
        methos : 'GET',
        url : 'https://naveropenapi.apigw.ntruss.com/map-reversegeocode/v2/gc?request=coordsToaddr&coords=127.1374275,37.332952899999995&orders=legalcode,admcode,addr,roadaddr&output=json',
        headers : {
          'X-NCP-APIGW-API-KEY-ID' : '49db75h9vw',
          'X-NCP-APIGW-API-KEY' : '7WS9EaGvmNvgx2Xw90KoY3KDpKOXZZ8e2YIK91lM'
        }
      }).then((response) => {
        console.log(response);
      }).catch((error) => {
        console.log("ERROR 발생");
      })
    }
  }
}


</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
}

#fixedFooter{
    position:fixed;
    left: 0px;
    bottom: 0px;
    width: 100%;
    text-align: center;
}

</style>



